<template>
  <ul class="list-group">
    <ProductListItem v-for="product in products" :product="product" @productSelect="onProductSelect">
    </ProductListItem>
  </ul>
</template>

<script>
import ProductListItem from './ProductListItem.vue';

export default {
  name: 'ProductList',
  components: {
    ProductListItem
  },
  props: ['products'],
  methods: {
    onProductSelect(product) {
      this.$emit('productSelect', product);
    }
  }
};
</script>

<style>
  ul {
    
  }
</style>